@mixin mobile {
    @media screen and (max-width: 480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width: 768px) {
        @content;
    }
}

@mixin laptop {
    @media screen and (max-width: 1200px) {
        @content;
    }
}

.formContainer {
    background-color: #ee7b7b;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper {
        background-color: #f5cece;
        padding: 20px 60px;
        border-radius: 12px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .logo {
            color: #a21414;
            font-weight: bolder;

            font-size: 32px;

        }

        .title {
            color: #a21414;
            font-size: small;

        }

        form {


            display: flex;
            flex-direction: column;
            gap: 15px;

            input {
                background-color: #f5cece;
                width: 250px;
                padding: 15px;
                border: none;
                border-bottom: 1px solid #ee7b7b;

                &::placeholder {
                    color: rgb(153, 153, 153);
                }
            }

            button {
                background-color: #ee7b7b;
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                cursor: pointer;
                border-radius: 15px;
            }


        }

        label {
            display: flex;
            align-items: center;
            gap: 10px;
            color: #ee7b7b;
            font-size: 12px;
            cursor: pointer;

            img {
                width: 32px;
            }
        }

        p {
            color: #a21414;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

.home {
    background-color: #ffbebe;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90%;
        }
    }

    .sidebar {
        flex: 1;
        background-color: #e55656;
        position: relative;

        .navbar {
            display: flex;
            align-items: center;
            background-color: #e81919;
            height: 50px;
            padding: 10px;
            justify-content: space-between;
            color: white;

            .logo {
                font-weight: bold;
                @include tablet{
                    display: none;
                }
            }

            .user {
                display: flex;
                gap: 10px;

                img {
                    background-color: #ddddf7;
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                button {
                    background-color: #fe8383;
                    color: #ddddf7;
                    font-size: 10px;
                    border: none;
                    cursor: pointer;
                    border-radius: 15%;
                    @include tablet{
                        position: absolute;
                        bottom: 10px;
                    }
                }
            }



        }

        .search {
            border-bottom: 1px solid gray;

            .searchForm {

                padding: 10px;

                input {
                    background-color: transparent;
                    border: none;
                    color: white;
                    outline: none;

                    &::placeholder {
                        color: lightgray;
                    }
                }
            }
        }

        .userChat {
            padding: 10px;
            display: flex;
            align-items: center;
            gap: 10px;
            color: white;
            cursor: pointer;

            &:hover {
                background-color: #e81919;
            }

            img {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                object-fit: cover;
            }

            .userChatInfo {
                span {
                    font-size: 18px;
                    font-weight: 500;
                }

                p {
                    font-size: 14px;
                    color: lightgray;
                }
            }

        }
    }

    .chat {
        flex: 2;

        .chatInfo {
            height: 50px;
            background-color: #f97070;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 10px;
            color: lightgray;
            font-weight: bold;
        }

        .chatIcons {
            display: flex;
            gap: 10px;

            img {
                height: 24px;
                cursor: pointer;
            }
        }

        .messages {
            background-color: #f7dddd;
            padding: 10px;
            height: calc(100% - 160px);
            overflow: scroll;

            .message {
                display: flex;
                gap: 20px;
                margin-bottom: 20px;

                .messageInfo {
                    display: flex;
                    flex-direction: column;
                    color: gray;
                    font-weight: 300;

                    img {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                }

                .messageContent {
                    max-width: 80%;
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    p {
                        background-color: white;
                        padding: 10px 20px;
                        border-radius: 0px 10px 10px 10px;
                        max-width: max-content;
                    }

                    img {
                        width: 50%;
                    }
                }

                &.owner {
                    flex-direction: row-reverse;

                    .messageContent {
                        align-items:flex-end;

                        p {
                            background-color: #ee7b7b;
                            color: white;
                            border-radius: 10px 0px 10px 10px;

                        }
                    }
                }
            }
        }

        .input {
            height: 50px;
            background-color: white;
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            input {
                width: 100%;
                border: none;
                outline: none;
                color: #a21414;
                font-size: 18px;

                &::placeholder {
                    color: lightgray;
                }
            }

            .send {
                display: flex;
                align-items: center;
                gap: 10px;

                img {
                    height: 24px;
                    cursor: pointer;

                }

                button {
                    border: none;
                    padding: 10px 15 px;
                    color: white;
                    background-color: #e81919;
                    cursor: pointer;
                }
            }
        }
    }
}